import api from './index.js'

// 车票相关API
export const ticketAPI = {
  // 搜索车票
  searchTickets(searchParams) {
    console.log('Searching tickets with params:', searchParams)
    return api.get(`http://localhost:50080/opt/schedules/search?departureTime=${searchParams.date}&endStationId=${searchParams.stopPos}&startStationId=${searchParams.startPos}`)
  },
  
  // 预订车票
  bookTicket(ticketData) {
    return api.post(`http://localhost:50081/orders/1/create?ticketId=${ticketData.ticketId}`, ticketData)
  },
}